import React, {useEffect, useState} from 'react';
import './TVSchedule.css';
import axios from 'axios';
import LazyLoad from 'react-lazy-load';
import Helmet from 'react-helmet';

const TVSchedule = () => {
    const[matchesSchedule, setMatchesSchedule] = useState();

    useEffect(() => {
        window.scrollTo(0, 0); // scroll top, when open page
    }, []);

    useEffect(() => {
        const fetchData = async () => {
            await axios.get('/tv/schedule')
            .then(response => {
                setMatchesSchedule(response.data && response.data.map((e, i) => {
                    return <div key={'matchesSchedule' + i} className="col">
                                <div className="channel"><LazyLoad offset={800}><img loading="lazy" src={e.channel} alt="channel" /></LazyLoad></div>
                                <div className="timeProgramme">
                                    <span>{e.time}</span>
                                    <span>{e.programme}</span>
                                </div>
                            </div>
                }));
            })
            .catch(err => {
                console.log(err);
            });
        }

        fetchData();
    }, []);

    return (
        <div id='tvScheduleOther'>
            <Helmet>
                <title>–¢–í —Ä–∞—Å–ø–∏—Å–∞–Ω–∏–µ –º–∞—Ç—á–µ–π - –Ω–∞ Legendary Football</title>
                <meta name="description" content="–ü–ª–∞–Ω–∏—Ä—É–µ—Ç–µ —Å–º–æ—Ç—Ä–µ—Ç—å —Ñ—É—Ç–±–æ–ª—å–Ω—ã–µ –º–∞—Ç—á–∏ –∏ –Ω–µ —Ö–æ—Ç–∏—Ç–µ –ø—Ä–æ–ø—É—Å—Ç–∏—Ç—å –Ω–∏ –æ–¥–Ω–æ–π –≤–∞–∂–Ω–æ–π –∏–≥—Ä—ã? –ü–æ–ª—É—á–∏—Ç–µ –∞–∫—Ç—É–∞–ª—å–Ω–æ–µ –¢–í —Ä–∞—Å–ø–∏—Å–∞–Ω–∏–µ –º–∞—Ç—á–µ–π –∏ —É–∑–Ω–∞–π—Ç–µ –≤—Ä–µ–º—è –∏ –∫–∞–Ω–∞–ª—ã —Ç—Ä–∞–Ω—Å–ª—è—Ü–∏–∏ –¥–ª—è –ª—é–±–∏–º—ã—Ö –∫–æ–º–∞–Ω–¥. –ù–∏–∫–∞–∫–∏—Ö —É–ø—É—â–µ–Ω–Ω—ã—Ö –º–æ–º–µ–Ω—Ç–æ–≤!" />
                <meta name="keywords" content="—Ç–≤ —Ä–∞—Å–ø–∏—Å–∞–Ω–∏–µ –º–∞—Ç—á–µ–π, —Ç–µ–ª–µ–ø–µ—Ä–µ–¥–∞—á–∞ —Å–ø–æ—Ä—Ç–∏–≤–Ω—ã—Ö —Å–æ–±—ã—Ç–∏–π, —Ç—Ä–∞–Ω—Å–ª—è—Ü–∏—è —Ñ—É—Ç–±–æ–ª—å–Ω—ã—Ö –º–∞—Ç—á–µ–π, –≥—Ä–∞—Ñ–∏–∫ —Å–ø–æ—Ä—Ç–∏–≤–Ω—ã—Ö —Ç—Ä–∞–Ω—Å–ª—è—Ü–∏–π, –ø—Ä–æ–≥—Ä–∞–º–º–∞ —Ç–µ–ª–µ–≤–∏–∑–∏–æ–Ω–Ω—ã—Ö –º–∞—Ç—á–µ–π, —Ä–∞—Å–ø–∏—Å–∞–Ω–∏–µ —Ñ—É—Ç–±–æ–ª—å–Ω—ã—Ö –∏–≥—Ä –Ω–∞ —Ç–≤, —Ç–≤-—Ç—Ä–∞–Ω—Å–ª—è—Ü–∏–∏ —Å–ø–æ—Ä—Ç–∞, —Å–ø–æ—Ä—Ç–∏–≤–Ω—ã–µ —Å–æ–±—ã—Ç–∏—è –≤ —ç—Ñ–∏—Ä–µ, –ø—Ä–æ–≥—Ä–∞–º–º–∞ –ø—Ä–æ—Å–º–æ—Ç—Ä–∞ —Ñ—É—Ç–±–æ–ª—å–Ω—ã—Ö –º–∞—Ç—á–µ–π, —Ç–≤-–≥—Ä–∞—Ñ–∏–∫ —Å–ø–æ—Ä—Ç–∏–≤–Ω—ã—Ö —Å–æ—Ä–µ–≤–Ω–æ–≤–∞–Ω–∏–π" />
            </Helmet>
            <h1 className="pageName">–¢–í —Ä–∞—Å–ø–∏—Å–∞–Ω–∏–µ</h1>
            <section>
                <div className="wrap">
                    {matchesSchedule}
                </div>
            </section>
        </div>
    );
};

export default TVSchedule;