import React, {useState, useEffect} from 'react';
import './UEFACountryRank.css';
import axios from 'axios';
import Tippy from '@tippyjs/react';
import 'tippy.js/dist/tippy.css';
import LazyLoad from 'react-lazy-load';
import Helmet from 'react-helmet';

import uefaLogo from '../../../assets/ico/uefaLogo.webp';

const UEFACountryRank = () => {
    const[uefaCountryRankSeason, setUefaCountryRankSeason] = useState();
    const[uefaCountryRank, setUefaCountryRank] = useState();

    useEffect(() => {
        window.scrollTo(0, 0); // scroll top, when open page
    }, []);

    useEffect(() => {
        const fetchData = async () => {
            await axios.get('/standings/uefacountryrankseason')
            .then(response => {
                setUefaCountryRankSeason(response.data && response.data.map((item, indx) => {
                    return <div key={'uefaCountryRankSeason' + indx}>
                        <Tippy content={'–°—É–º–º–∞ –æ—á–∫–æ–≤ –∑–∞ —Å–µ–∑–æ–Ω 20' + item.seasonLast5.split('/')[0] + '/20' + item.seasonLast5.split('/')[1]}><span>{item.seasonLast5}</span></Tippy>
                        <Tippy content={'–°—É–º–º–∞ –æ—á–∫–æ–≤ –∑–∞ —Å–µ–∑–æ–Ω 20' + item.seasonLast4.split('/')[0] + '/20' + item.seasonLast4.split('/')[1]}><span>{item.seasonLast4}</span></Tippy>
                        <Tippy content={'–°—É–º–º–∞ –æ—á–∫–æ–≤ –∑–∞ —Å–µ–∑–æ–Ω 20' + item.seasonLast3.split('/')[0] + '/20' + item.seasonLast3.split('/')[1]}><span>{item.seasonLast3}</span></Tippy>
                        <Tippy content={'–°—É–º–º–∞ –æ—á–∫–æ–≤ –∑–∞ —Å–µ–∑–æ–Ω 20' + item.seasonLast2.split('/')[0] + '/20' + item.seasonLast2.split('/')[1]}><span>{item.seasonLast2}</span></Tippy>
                        <Tippy content={'–°—É–º–º–∞ –æ—á–∫–æ–≤ –∑–∞ —Ç–µ–∫—É—â–∏–π —Å–µ–∑–æ–Ω'}><span className='current'>{item.seasonCurrent}</span></Tippy>
                    </div>
                }));
            })
            .catch(err => {
                console.log(err);
            });

            axios.get('/standings/uefacountryrank')
            .then(response => {
                setUefaCountryRank(response.data && response.data.map((item, indx) => {
                    return <div className="col wrap" key={'uefaCountryRank' + indx}>
                    <div>
                        <span className='place'>{item.place}<span>.</span></span>
                        <div className='flagName'><LazyLoad offset={800}><Tippy content={item.name}><img loading="lazy" src={'https://terrikon.com' + item.flag} alt={item.name} /></Tippy></LazyLoad><span>{item.name}</span></div>
                        <span>{item.quantity}</span>
                        <span className='total'>{item.total}</span>
                    </div>
                    <div>
                        <span>{item.totalLast5}</span>
                        <span>{item.totalLast4}</span>
                        <span>{item.totalLast3}</span>
                        <span>{item.totalLast2}</span>
                        <span className='current'>{item.totalCurrent}</span>
                    </div>
                </div>
                }));
            })
            .catch(err => {
                console.log(err);
            });
        }

        fetchData();
    }, []); 

    return (
        <div id='uefaCountryRankOther'>
            <Helmet>
                <title>–¢–∞–±–ª–∏—Ü–∞ –∫–æ—ç—Ñ—Ñ–∏—Ü–∏–µ–Ω—Ç–æ–≤ –£–ï–§–ê (UEFA) - –Ω–∞ Legendary Football</title>
                <meta name="description" content="–ò–∑—É—á–∏—Ç–µ —Ç–∞–±–ª–∏—Ü—É –∫–æ—ç—Ñ—Ñ–∏—Ü–∏–µ–Ω—Ç–æ–≤ –£–ï–§–ê (UEFA) –≤ –æ–Ω–ª–∞–π–Ω —Ä–µ–∂–∏–º–µ - —Ä–µ–π—Ç–∏–Ω–≥ –∫–æ–º–∞–Ω–¥ –∏ –∞—Å—Å–æ—Ü–∏–∞—Ü–∏–π –Ω–∞ –µ–≤—Ä–æ–ø–µ–π—Å–∫–æ–π —Ñ—É—Ç–±–æ–ª—å–Ω–æ–π –∞—Ä–µ–Ω–µ. –£–∑–Ω–∞–π—Ç–µ –æ —Ä–µ–π—Ç–∏–Ω–≥–æ–≤—ã—Ö –ø–æ–∑–∏—Ü–∏—è—Ö, —Å—Ç–∞—Ç–∏—Å—Ç–∏–∫–µ –∏ –∑–Ω–∞—á–∏–º–æ—Å—Ç–∏ –∫–ª—É–±–æ–≤ –∏ —Å—Ç—Ä–∞–Ω –≤ –µ–≤—Ä–æ–∫—É–±–∫–∞—Ö." />
                <meta name="keywords" content="—Ç–∞–±–ª–∏—Ü–∞ –∫–æ—ç—Ñ—Ñ–∏—Ü–∏–µ–Ω—Ç–æ–≤ —É–µ—Ñ–∞, —Ä–µ–π—Ç–∏–Ω–≥ –∫–æ–º–∞–Ω–¥ —É–µ—Ñ–∞, —Ä–µ–π—Ç–∏–Ω–≥ –∞—Å—Å–æ—Ü–∏–∞—Ü–∏–π —É–µ—Ñ–∞, –µ–≤—Ä–æ–ø–µ–π—Å–∫–∞—è —Ñ—É—Ç–±–æ–ª—å–Ω–∞—è –∞—Ä–µ–Ω–∞, —Ä–µ–π—Ç–∏–Ω–≥–æ–≤—ã–µ –ø–æ–∑–∏—Ü–∏–∏ –∫–ª—É–±–æ–≤ –∏ —Å—Ç—Ä–∞–Ω, –µ–≤—Ä–æ–∫—É–±–∫–∏ —É–µ—Ñ–∞, –æ–±–Ω–æ–≤–ª–µ–Ω–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ, uefa –∫–æ—ç—Ñ—Ñ–∏—Ü–∏–µ–Ω—Ç, –µ–≤—Ä–æ–ø–µ–π—Å–∫–∏–π —Ñ—É—Ç–±–æ–ª, —Ñ—É—Ç–±–æ–ª—å–Ω—ã–µ –∫–ª—É–±—ã –∏ –∞—Å—Å–æ—Ü–∏–∞—Ü–∏–∏, –æ–Ω–ª–∞–π–Ω" />
            </Helmet>
            <h1 className="pageName">–†–µ–π—Ç–∏–Ω–≥ –∞—Å—Å–æ—Ü–∏–∞—Ü–∏–π –£–ï–§–ê</h1>
            <section>
                <div className="uefaTable">
                    <div className="logoWrap">
                        <LazyLoad offset={800}><Tippy content='UEFA'><img loading="lazy" src={uefaLogo} alt="uefaLogo" /></Tippy></LazyLoad>
                    </div>
                    <div className="col">
                        <div>
                            <Tippy content="–ü–æ–∑–∏—Ü–∏—è"><span>#</span></Tippy>
                            <Tippy content="–°—Ç—Ä–∞–Ω–∞"><span>–°—Ç—Ä–∞–Ω–∞</span></Tippy>
                            <Tippy content="–ö–æ–ª–∏—á–µ—Å—Ç–≤–æ –∫–ª—É–±–æ–≤"><span>–ö–ª—É–±—ã</span></Tippy>
                            <Tippy content="–°—É–º–º–∞ –æ—á–∫–æ–≤"><span className='total'>–°—É–º–º–∞</span></Tippy>
                        </div>
                        {uefaCountryRankSeason}
                    </div>
                    {uefaCountryRank}
                </div>
            </section>
        </div>
    );
};

export default UEFACountryRank;