import React, { useEffect, useState } from 'react';
import './FifaRanking.css';
import $ from 'jquery';
import axios from 'axios';
import Tippy from '@tippyjs/react';
import 'tippy.js/dist/tippy.css';
import LazyLoad from 'react-lazy-load';
import Helmet from 'react-helmet';

import fifaLogo from '../../../assets/ico/fifaLogo.webp';

const FifaRanking = () => {
    const[fifaCountryRank, setFifaCountryRank] = useState();
    const[expandToggle, setExpandToggle] = useState(50);

    useEffect(() => {
        window.scrollTo(0, 0); // scroll top, when open page
    }, []);

    useEffect(() => {
        const fetchData = async () => {
            await axios.get('/standings/fifaranking')
            .then(response => {
                setFifaCountryRank(response.data && response.data.splice(0, expandToggle).map((item, indx) => {
                    return <div className="col wrap" key={'fifaRanking' + indx}>
                    <div>
                        <span className='place'>{item.place}.<span></span></span>
                        <div className='flagName'><LazyLoad offset={800}><Tippy content={item.name}><img loading="lazy" src={item.flag} alt={item.name} /></Tippy></LazyLoad><span>{item.name}</span></div>
                        <span className={item.difference[0] === '=' ? 'diffEqual' : false || item.difference[0] === '‚Üë' ? 'diffUp' : false || item.difference[0] === '‚Üì' ? 'diffDown' : false}>{item.difference}</span>
                        <span className='total'>{item.points}</span>
                    </div>
                    <div>
                        <span>{item.pointsDiff}</span>
                        <span>{item.association}</span>
                    </div>
                </div>
                }));
            })
            .catch(err => {
                console.log(err);
            });
        }

        fetchData();
    }, [expandToggle]); 

    const fifaCountryRankToggle = () => {
        setExpandToggle(expandToggle + 20);
        $('#fifaRank > section > div > div.more > a > span').text('–ü–æ–¥—Ä–æ–±–Ω–µ–µ');
    }

    return (
        <div id='fifaRankOther'>
            <Helmet>
                <title>–†–µ–π—Ç–∏–Ω–≥ –§–ò–§–ê (FIFA) - –Ω–∞ Legendary Football</title>
                <meta name="description" content="–ò–∑—É—á–∏—Ç–µ —Ç–∞–±–ª–∏—Ü—É –∫–æ—ç—Ñ—Ñ–∏—Ü–∏–µ–Ω—Ç–æ–≤ –§–ò–§–ê (FIFA) –≤ –æ–Ω–ª–∞–π–Ω —Ä–µ–∂–∏–º–µ - —Ä–µ–π—Ç–∏–Ω–≥ —Å–±–æ—Ä–Ω—ã—Ö –Ω–∞ –º–∏—Ä–æ–≤–æ–π —Ñ—É—Ç–±–æ–ª—å–Ω–æ–π –∞—Ä–µ–Ω–µ. –£–∑–Ω–∞–π—Ç–µ –æ —Ä–µ–π—Ç–∏–Ω–≥–æ–≤—ã—Ö –ø–æ–∑–∏—Ü–∏—è—Ö, —Å—Ç–∞—Ç–∏—Å—Ç–∏–∫–µ –∏ –∑–Ω–∞—á–∏–º–æ—Å—Ç–∏ —Å–±–æ—Ä–Ω—ã—Ö –∫–æ–º–∞–Ω–¥ –≤ –º–∏—Ä–µ." />
                <meta name="keywords" content="—Ç–∞–±–ª–∏—Ü–∞ –∫–æ—ç—Ñ—Ñ–∏—Ü–∏–µ–Ω—Ç–æ–≤ —Ñ–∏—Ñ–∞, —Ä–µ–π—Ç–∏–Ω–≥ –∫–æ–º–∞–Ω–¥ —Ñ–∏—Ñ–∞, —Ä–µ–π—Ç–∏–Ω–≥ –∞—Å—Å–æ—Ü–∏–∞—Ü–∏–π —Ñ–∏—Ñ–∞, –º–∏—Ä–æ–≤–∞—è —Ñ—É—Ç–±–æ–ª—å–Ω–∞—è –∞—Ä–µ–Ω–∞, —Ä–µ–π—Ç–∏–Ω–≥–æ–≤—ã–µ –ø–æ–∑–∏—Ü–∏–∏ —Å—Ç—Ä–∞–Ω —Ñ–∏—Ñ–∞, –æ–±–Ω–æ–≤–ª–µ–Ω–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ, fifa –∫–æ—ç—Ñ—Ñ–∏—Ü–∏–µ–Ω—Ç, –º–∏—Ä–æ–≤–æ–π —Ñ—É—Ç–±–æ–ª, —Ñ—É—Ç–±–æ–ª—å–Ω—ã–µ –∫–ª—É–±—ã –∏ –∞—Å—Å–æ—Ü–∏–∞—Ü–∏–∏, –æ–Ω–ª–∞–π–Ω, —Ä–µ–π—Ç–∏–Ω–≥ —Ñ–∏—Ñ–∞" />
            </Helmet>
            <h1 className="pageName">–†–µ–π—Ç–∏–Ω–≥ –§–ò–§–ê</h1>
            <section>
                <div className="uefaTable">
                    <div className="logoWrap">
                        <LazyLoad offset={800}><Tippy content='FIFA'><img loading="lazy" src={fifaLogo} alt="fifaLogo" /></Tippy></LazyLoad>
                    </div>
                    <div className="col">
                        <div>
                            <Tippy content="–ü–æ–∑–∏—Ü–∏—è"><span>#</span></Tippy>
                            <Tippy content="–°—Ç—Ä–∞–Ω–∞"><span>–°—Ç—Ä–∞–Ω–∞</span></Tippy>
                            <Tippy content="–ò–∑–º–µ–Ω–µ–Ω–∏—è –≤ –ø–æ–∑–∏—Ü–∏–∏"><span>–ò–∑–º–µ–Ω–µ–Ω–∏—è</span></Tippy>
                            <Tippy content="–°—É–º–º–∞ –æ—á–∫–æ–≤"><span className='total'>–û—á–∫–∏</span></Tippy>
                        </div>
                        <div>
                            <Tippy content="–†–∞–∑–Ω–∏—Ü–∞ —Å –ø—Ä–µ–¥—ã–¥—É—â–∏–º–∏ –æ—á–∫–∞–º–∏"><span>+/-</span></Tippy>
                            <Tippy content="–ê—Å—Å–æ—Ü–∏–∞—Ü–∏—è"><span>–ê—Å—Å–æ—Ü–∏–∞—Ü–∏—è</span></Tippy>
                        </div>
                    </div>
                    {fifaCountryRank}
                    <div className="more">
                        <span onClick={fifaCountryRankToggle}>–†–∞–∑–≤–µ—Ä–Ω—É—Ç—å —Å–ø–∏—Å–æ–∫</span>
                    </div>
                </div>
            </section>
        </div>
    );
};

export default FifaRanking;