import React, {useState, useEffect} from 'react';
import './Blogs.css';
import { Link } from 'react-router-dom';
import axios from 'axios';
import cyrillicToTranslit from 'cyrillic-to-translit-js';
import $ from 'jquery';
import LazyLoad from 'react-lazy-load';
import Helmet from 'react-helmet';

const Blogs = () => {
    const[news, setNews] = useState();

    useEffect(() => {
        window.scrollTo(0, 0); // scroll top, when open page
    }, []);

    useEffect(() => {
        const fetchData = async () => {
            await axios.get('/news/blogs')
            .then(response => {
                setNews(response.data && response.data.reverse().map((e) => {
                    let date = new Date(e.date);
                    let day = String(date.getDate()).length < 2 ? '0' + String(date.getDate()) : String(date.getDate());
                    let month = String(date.getMonth()).length < 2 ? '0' + String(date.getMonth() + 1) : String(date.getMonth() + 1);
                    let year = date.getFullYear();
                    let hours = String(date.getHours()).length < 2 ? '0' + String(date.getHours()) : String(date.getHours());
                    let minutes = String(date.getMinutes()).length < 2 ? '0' + String(date.getMinutes()) : String(date.getMinutes());
    
                    const animIn = () => { // anim mouse in
                        $(`.newsHr #${'id' + e.id} .img img`).css({'transform': 'scale(1.04)'});
                        $(`.newsHr #${'id' + e.id}`).css({'boxShadow': '0px 0px 15px 1px #000'});
                        $(`.newsHr #${'id' + e.id} h3`).css({'backgroundColor': 'rgba(0, 0, 0, 0.7)'}).css({'color': 'rgb(224, 164, 79)'});
                        $(`.newsHr #${'id' + e.id} .img img`).css({'opacity': '1'});
                    }
                    const animOut = () => { // anim mouse out
                        $(`.newsHr #${'id' + e.id} .img img`).css({'transform': 'scale(1)'});
                        $(`.newsHr #${'id' + e.id}`).css({'boxShadow': '0px 0px 0px 0px #000'});
                        $(`.newsHr #${'id' + e.id} h3`).css({'backgroundColor': 'rgba(0, 0, 0, 0.1)'}).css({'color': 'rgb(255, 255, 255)'});
                        $(`.newsHr #${'id' + e.id} .img img`).css({'opacity': '0.8'});
                    }
                    return  <div key={'blogs' + e.id} className="cart" id={'id' + e.id} onMouseEnter={animIn} onMouseLeave={animOut}>
                                <Link to={`/news/read/${e.id + '-' + cyrillicToTranslit().transform(e.title).replace(/[^a-zA-Z\s]/g, '').replace(/\s+/g, '-').toLowerCase()}`}>
                                    <div className="img"><LazyLoad offset={800}><img loading="lazy" alt={e.title} src={e.img} /></LazyLoad></div>
                                    <h3>{e.title}</h3>
                                    <span className='date'>{day + '-' + month + '-' + year + ' | ' + hours + ':' + minutes} <span className='views'>üëÅ {`${e && e.views?.split(',').length > 0 ? e.views?.split(',').length : '0'}`}</span></span>
                                    <span className='category'><span className="likes">‚ù§ {`${e && e.likes?.split(',').length > 0 ? e.likes?.split(',').length : '0'}`}</span> {`#${e.category}`}</span>
                                </Link>
                            </div>
                }));
            })
            .catch(err => {
                console.log(err);
            });
        }

        fetchData();
    }, []);

    return (
        <div id='blogsOther' className='newsHr'>
            <Helmet>
                <title>–ë–ª–æ–≥–∏, –º–Ω–µ–Ω–∏—è –∞–≤—Ç–æ—Ä–æ–≤ –∏ –∞–Ω–∞–ª–∏—Ç–∏–∫–∞ - –Ω–∞ Legendary Football</title>
                <meta name="description" content="–ù–∞—à –±–ª–æ–≥ –ø—Ä–µ–¥–ª–∞–≥–∞–µ—Ç —ç–∫—Å–∫–ª—é–∑–∏–≤–Ω—ã–π –∫–æ–Ω—Ç–µ–Ω—Ç, –∞ —Ç–∞–∫–∂–µ –º–Ω–µ–Ω–∏—è –∏ –∞–Ω–∞–ª–∏—Ç–∏–∫—É –æ—Ç –Ω–∞—à–∏—Ö –∞–≤—Ç–æ—Ä–æ–≤. –û—Å—Ç–∞–≤–∞–π—Ç–µ—Å—å –≤ –∫—É—Ä—Å–µ —Ñ—É—Ç–±–æ–ª—å–Ω—ã—Ö —Å–æ–±—ã—Ç–∏–π –∏ –∏–Ω—Ç–µ—Ä–µ—Å–Ω—ã—Ö —Ñ–∞–∫—Ç–æ–≤, –∏ –æ—Ç–∫—Ä–æ–π—Ç–µ –¥–ª—è —Å–µ–±—è –Ω–æ–≤—ã–µ –≥—Ä–∞–Ω–∏ —É–≤–ª–µ—á–µ–Ω–∏–π –≤–º–µ—Å—Ç–µ —Å –Ω–∞–º–∏." />
                <meta name="keywords" content="—Ñ—É—Ç–±–æ–ª, –Ω–æ–≤–æ—Å—Ç–∏, –æ—Ñ—Ñ—Ç–æ–ø—ã, —Å–ø–æ—Ä—Ç, —Å–æ–±—ã—Ç–∏—è, —ç–∫—Å–∫–ª—é–∑–∏–≤–Ω—ã–π –∫–æ–Ω—Ç–µ–Ω—Ç, —É–≤–ª–µ–∫–∞—Ç–µ–ª—å–Ω—ã–µ –∏—Å—Ç–æ—Ä–∏–∏, –±–ª–æ–≥–∏, –º–Ω–µ–Ω–∏—è –∞–≤—Ç–æ—Ä–æ–≤, –∞–Ω–∞–ª–∏—Ç–∏–∫–∞, –º–Ω–µ–Ω–∏—è —Å–ø–µ—Ü–∏–∞–ª–∏—Å—Ç–æ–≤, –∏–Ω—Å–∞–π–¥—ã, —Ç—Ä–∞–Ω—Å—Ñ–µ—Ä—ã, —Ñ—É—Ç–±–æ–ª—å–Ω—ã–µ –º–∞—Ç—á–∏, —Ñ—É—Ç–±–æ–ª—å–Ω—ã–µ –∫–ª—É–±—ã, —Å–ø–æ—Ä—Ç–∏–≤–Ω—ã–µ —Å–æ–±—ã—Ç–∏—è, —Å–ø–æ—Ä—Ç–∏–≤–Ω—ã–µ —Ñ–∞–∫—Ç—ã" />
            </Helmet>
            <h1 className="pageName">–ë–ª–æ–≥–∏</h1>
            <section>
                {news}
            </section>
        </div>
    );
};

export default Blogs;