import React, { useEffect, useState } from 'react';
import './Odds.css';
import axios from 'axios';
import Tippy from '@tippyjs/react';
import 'tippy.js/dist/tippy.css';
import LazyLoad from 'react-lazy-load';
import Helmet from 'react-helmet';

const Forecasts = () => {
    const[forecasts, setForecasts] = useState();

    useEffect(() => {
        window.scrollTo(0, 0); // scroll top, when open page
    }, []);

    useEffect(() => {
        function convertGermanToclientTime(germanTime) {
            // –†–∞–∑–±–∏–≤–∞–µ–º —Å—Ç—Ä–æ–∫—É –≤—Ä–µ–º–µ–Ω–∏ –Ω–∞ —á–∞—Å—ã –∏ –º–∏–Ω—É—Ç—ã
            const [hours, minutes] = germanTime.split(':').map(Number);
          
            // –°–æ–∑–¥–∞–µ–º –æ–±—ä–µ–∫—Ç Date —Å —Ç–µ–∫—É—â–µ–π –¥–∞—Ç–æ–π –∏ –≤—Ä–µ–º–µ–Ω–µ–º –≤ –Ω–µ–º–µ—Ü–∫–æ–π –≤—Ä–µ–º–µ–Ω–Ω–æ–π –∑–æ–Ω–µ
            const germanDate = new Date();
            germanDate.setHours(hours);
            germanDate.setMinutes(minutes);

            const clientUTCOffset = new Date();
          
            // –î–æ–±–∞–≤–ª—è–µ–º —Ä–∞–∑–Ω–∏—Ü—É –º–µ–∂–¥—É –Ω–µ–º–µ—Ü–∫–∏–º –∏ –∏—Ä–∞–Ω—Å–∫–∏–º –≤—Ä–µ–º–µ–Ω–µ–º (2.5 —á–∞—Å–∞)
            const clientDate = new Date(germanDate.getTime() + ((-clientUTCOffset.getTimezoneOffset() / 60) - 3) * 60 * 60 * 1000);
          
            // –ü–æ–ª—É—á–∞–µ–º –∏—Ä–∞–Ω—Å–∫–æ–µ –≤—Ä–µ–º—è –≤ —Ñ–æ—Ä–º–∞—Ç–µ "—á—á:–º–º"
            const clientTime = `${clientDate.getHours()}:${clientDate.getMinutes().toString().padStart(2, '0')}`;
          
            return clientTime;
          }
          
        const fetchData = async () => {
            await axios.get('/forecasts/odds')
            .then(response => {
                setForecasts(response.data && response.data.map((e, i) => {
                    return  <div className="col" key={'forecasts' + i}>
                                <div>
                                    <div className="head"><span>{e.lCountryName}</span>&nbsp;&nbsp;|&nbsp;&nbsp;<span>{e.lName}</span></div>
                                    <div className="center">
                                        <div className="home"><LazyLoad offset={800}><Tippy content={e.hName}><img loading="lazy" src={e.hLogo} alt={e.hName} /></Tippy></LazyLoad><span>{e.hName}</span></div>
                                        <div className="dateTime"><span>{e.date}</span><span>{convertGermanToclientTime(e.time)}</span></div>
                                        <div className="away"><span>{e.aName}</span><LazyLoad offset={800}><Tippy content={e.aName}><img loading="lazy" src={e.aLogo} alt={e.aName} /></Tippy></LazyLoad></div>
                                    </div>
                                </div>
                                <div className="coefWrap">
                                    <div className="w1">
                                        <Tippy content="–ü–æ–±–µ–¥–∞ –ø–µ—Ä–≤–æ–π –∫–æ–º–∞–Ω–¥—ã"><span>–ü1</span></Tippy>
                                        <span>{e.w1 === '' ? '-' : e.w1}</span>
                                    </div>
                                    <div className="w1">
                                        <Tippy content="–ù–∏—á—å—è"><span>–•</span></Tippy>
                                        <span>{e.draw === '' ? '-' : e.draw}</span>
                                    </div>
                                    <div className="w1">
                                        <Tippy content="–ü–æ–±–µ–¥–∞ –≤—Ç–æ—Ä–æ–π –∫–æ–º–∞–Ω–¥—ã"><span>–ü2</span></Tippy>
                                        <span>{e.w2 === '' ? '-' : e.w2}</span>
                                    </div>
                                    <div className="w1">
                                        <Tippy content="–ë—É–¥–µ—Ç –∑–∞–±–∏—Ç–æ –º–µ–Ω—å—à–µ 2-—É—Ö (–≤–∫–ª—é—á–∏—Ç–µ–ª—å–Ω–æ) –º—è—á–µ–π"><span>–¢–ú 2.5</span></Tippy>
                                        <span>{e.totalU === '' ? '-' : e.totalU}</span>
                                    </div>
                                    <div className="w1">
                                        <Tippy content="–ë—É–¥–µ—Ç –∑–∞–±–∏—Ç–æ –±–æ–ª—å—à–µ 3-—ë—Ö (–≤–∫–ª—é—á–∏—Ç–µ–ª—å–Ω–æ) –º—è—á–µ–π"><span>–¢–ë 2.5</span></Tippy>
                                        <span>{e.totalO === '' ? '-' : e.totalO}</span>
                                    </div>
                                </div>
                            </div>
                }));
            })
            .catch(err => {
                console.log(err);
            });
        }

        fetchData();
    }, []);
        
    return (
        <div id='forecastsOther'>
            <Helmet>
                <title>–ö–æ—Ç–∏—Ä–æ–≤–∫–∏ –Ω–∞ –º–∞—Ç—á–∏ - –Ω–∞ Legendary Football</title>
                <meta name="description" content="–£–∑–Ω–∞–π—Ç–µ –∞–∫—Ç—É–∞–ª—å–Ω—ã–µ –∫–æ—Ç–∏—Ä–æ–≤–∫–∏ –Ω–∞ –º–∞—Ç—á–∏ –≤–∞—à–µ–π –ª—é–±–∏–º–æ–π –∫–æ–º–∞–Ω–¥—ã. –ê–Ω–∞–ª–∏–∑–∏—Ä—É–π—Ç–µ –∫–æ—ç—Ñ—Ñ–∏—Ü–∏–µ–Ω—Ç—ã –∏ –¥–µ–ª–∞–π—Ç–µ –æ—Å–º—ã—Å–ª–µ–Ω–Ω—ã–µ —Å—Ç–∞–≤–∫–∏ —Å –Ω–∞—à–∏–º–∏ –Ω–∞–¥–µ–∂–Ω—ã–º–∏ –∫–æ—Ç–∏—Ä–æ–≤–∫–∞–º–∏." />
                <meta name="keywords" content="–∫–æ—Ç–∏—Ä–æ–≤–∫–∏ –Ω–∞ –º–∞—Ç—á–∏, —Å—Ç–∞–≤–∫–∏ –Ω–∞ —Å–ø–æ—Ä—Ç, —Å–ø–æ—Ä—Ç–∏–≤–Ω—ã–µ —Å—Ç–∞–≤–∫–∏, –∫–æ—ç—Ñ—Ñ–∏—Ü–∏–µ–Ω—Ç—ã –Ω–∞ —Ñ—É—Ç–±–æ–ª, –∫–æ—Ç–∏—Ä–æ–≤–∫–∏ –Ω–∞ –±–∞—Å–∫–µ—Ç–±–æ–ª, —Ç–µ–Ω–Ω–∏—Å–Ω—ã–µ —Å—Ç–∞–≤–∫–∏, —Å—Ç–∞–≤–∫–∏ –Ω–∞ —Å–ø–æ—Ä—Ç–∏–≤–Ω—ã–µ —Å–æ–±—ã—Ç–∏—è, —Å–ø–æ—Ä—Ç–∏–≤–Ω—ã–µ –∫–æ—ç—Ñ—Ñ–∏—Ü–∏–µ–Ω—Ç—ã, —Å–ø–æ—Ä—Ç–∏–≤–Ω—ã–µ —Å—Ç–∞–≤–∫–∏ –æ–Ω–ª–∞–π–Ω, –∞–∫—Ç—É–∞–ª—å–Ω—ã–µ –∫–æ—Ç–∏—Ä–æ–≤–∫–∏, —Å–ø–æ—Ä—Ç–∏–≤–Ω—ã–µ –ø–æ–±–µ–¥—ã" />
            </Helmet>
            <h1 className="pageName">–ö–æ—Ç–∏—Ä–æ–≤–∫–∏ –Ω–∞ –º–∞—Ç—á–∏</h1>
            <section>
                {forecasts}
            </section>
        </div>
    );
};

export default Forecasts;