import React, { useEffect, useState } from 'react';
import './Broadcasts.css';
import Tippy from '@tippyjs/react';
import LazyLoad from 'react-lazy-load';
import axios from 'axios';
import { Link } from 'react-router-dom';
import Helmet from 'react-helmet';
import cyrillicToTranslit from 'cyrillic-to-translit-js';

const Broadcasts = () => {
    const[item, setItem] = useState();

    useEffect(() => {
        window.scrollTo(0, 0); // scroll top, when open page
    }, []);

    useEffect(() => {
        function convertGermanToclientTime(germanTime) {
            // –†–∞–∑–±–∏–≤–∞–µ–º —Å—Ç—Ä–æ–∫—É –≤—Ä–µ–º–µ–Ω–∏ –Ω–∞ —á–∞—Å—ã –∏ –º–∏–Ω—É—Ç—ã
            const [hours, minutes] = germanTime.split(':').map(Number);
          
            // –°–æ–∑–¥–∞–µ–º –æ–±—ä–µ–∫—Ç Date —Å —Ç–µ–∫—É—â–µ–π –¥–∞—Ç–æ–π –∏ –≤—Ä–µ–º–µ–Ω–µ–º –≤ –Ω–µ–º–µ—Ü–∫–æ–π –≤—Ä–µ–º–µ–Ω–Ω–æ–π –∑–æ–Ω–µ
            const germanDate = new Date();
            germanDate.setHours(hours);
            germanDate.setMinutes(minutes);

            const clientUTCOffset = new Date();
          
            // –î–æ–±–∞–≤–ª—è–µ–º —Ä–∞–∑–Ω–∏—Ü—É –º–µ–∂–¥—É –Ω–µ–º–µ—Ü–∫–∏–º –∏ –∏—Ä–∞–Ω—Å–∫–∏–º –≤—Ä–µ–º–µ–Ω–µ–º (2.5 —á–∞—Å–∞)
            const clientDate = new Date(germanDate.getTime() + ((-clientUTCOffset.getTimezoneOffset() / 60) - 4) * 60 * 60 * 1000);
          
            // –ü–æ–ª—É—á–∞–µ–º –∏—Ä–∞–Ω—Å–∫–æ–µ –≤—Ä–µ–º—è –≤ —Ñ–æ—Ä–º–∞—Ç–µ "—á—á:–º–º"
            const clientTime = `${clientDate.getHours()}:${clientDate.getMinutes().toString().padStart(2, '0')}`;
          
            return clientTime;
          }

        const fetchData = async () => {
            await axios.get('/broadcasts/get')
            .then(response => {
                setItem(response.data && response.data.reverse().splice(0, 8).map((e) => {    
                    return <Tippy content='–°–º–æ—Ç—Ä–µ—Ç—å' key={'broadcast' + e.id}>
                                <Link to={`/broadcast/watch/${e.id + '-' + cyrillicToTranslit().transform(e.hName + '-' + e.aName + '-' + e.lName).replace(/[^a-zA-Z\s]/g, '').replace(/\s+/g, '-').toLowerCase()}`} id={'broadcast' + e.id}>
                                    <div className="col">
                                        <div><LazyLoad offset={800}><Tippy content={e.hName}><img loading="lazy" src={e.hLogo} alt={e.hName} /></Tippy></LazyLoad><span>{e.hName.slice(0, 8)}</span></div>
                                        <div className='timeLive'>
                                            <LazyLoad offset={800}><Tippy content={e.lName}><img loading="lazy" src={e.lLogo} alt={e.lName} /></Tippy></LazyLoad>
                                            {e.broadcastLink === null ? <span>{convertGermanToclientTime(e.time)}</span> : <span style={{color: 'red', letterSpacing: '1.3px'}}>live</span>}
                                        </div>
                                        <div><span>{e.aName.slice(0, 8)}</span><LazyLoad offset={800}><Tippy content={e.aName}><img loading="lazy" src={e.aLogo} alt={e.aName} /></Tippy></LazyLoad></div>
                                    </div>
                                </Link>
                            </Tippy>
                })); 
            })
            .catch(err => {
                console.log(err);
            }); 
        }
        
        fetchData();
    }, []); 

    return (
        <div className='otherBroadcasts'>
            <Helmet>
                <title>–û–Ω–ª–∞–π–Ω —Ç—Ä–∞–Ω—Å–ª—è—Ü–∏–∏ –º–∞—Ç—á–µ–π - –Ω–∞ Legendary Football</title>
                <meta name="description" content="–°–º–æ—Ç—Ä–∏—Ç–µ –æ–Ω–ª–∞–π–Ω —Ç—Ä–∞–Ω—Å–ª—è—Ü–∏—é –º–∞—Ç—á–µ–π —Å–≤–æ–∏—Ö –ª—é–±–∏–º—ã—Ö –∫–æ–º–∞–Ω–¥ –∏ —Å–ø–æ—Ä—Ç–∏–≤–Ω—ã—Ö —Å–æ–±—ã—Ç–∏–π –Ω–∞ –Ω–∞—à–µ–º —Å–∞–π—Ç–µ. –ù–µ —É–ø—É—Å—Ç–∏—Ç–µ –Ω–∏ –æ–¥–Ω–æ–π –≤–∞–∂–Ω–æ–π –∏–≥—Ä—ã, –Ω–∞—Å–ª–∞–∂–¥–∞–π—Ç–µ—Å—å –ø—Ä—è–º—ã–º–∏ —Ç—Ä–∞–Ω—Å–ª—è—Ü–∏—è–º–∏. –û–∫—É–Ω–∏—Ç–µ—Å—å –≤ –º–∏—Ä —Å–ø–æ—Ä—Ç–∏–≤–Ω—ã—Ö —ç–º–æ—Ü–∏–π –∏ –∑–∞—Ö–≤–∞—Ç—ã–≤–∞—é—â–∏—Ö –º–∞—Ç—á–µ–π, –≤—Å—ë —ç—Ç–æ –¥–æ—Å—Ç—É–ø–Ω–æ –≤ —Ä–µ–∂–∏–º–µ —Ä–µ–∞–ª—å–Ω–æ–≥–æ –≤—Ä–µ–º–µ–Ω–∏. –ü–æ–¥–∫–ª—é—á–∞–π—Ç–µ—Å—å –∫ –æ–Ω–ª–∞–π–Ω —Ç—Ä–∞–Ω—Å–ª—è—Ü–∏–∏ –º–∞—Ç—á–µ–π —É–∂–µ —Å–µ–≥–æ–¥–Ω—è!" />
                <meta name="keywords" content="–æ–Ω–ª–∞–π–Ω —Ç—Ä–∞–Ω—Å–ª—è—Ü–∏—è –º–∞—Ç—á–µ–π, –ø—Ä—è–º—ã–µ —Ç—Ä–∞–Ω—Å–ª—è—Ü–∏–∏ —Å–ø–æ—Ä—Ç–∞, —Å–ø–æ—Ä—Ç–∏–≤–Ω—ã–µ —Ç—Ä–∞–Ω—Å–ª—è—Ü–∏–∏ –æ–Ω–ª–∞–π–Ω, —Å–º–æ—Ç—Ä–µ—Ç—å –º–∞—Ç—á–∏ –≤ —Ä–µ–∞–ª—å–Ω–æ–º –≤—Ä–µ–º–µ–Ω–∏, –æ–Ω–ª–∞–π–Ω —Å–ø–æ—Ä—Ç, –ø—Ä—è–º–æ–π —ç—Ñ–∏—Ä —Å–ø–æ—Ä—Ç–∏–≤–Ω—ã—Ö —Å–æ–±—ã—Ç–∏–π, —Ç—Ä–∞–Ω—Å–ª—è—Ü–∏–∏ —Ñ—É—Ç–±–æ–ª–∞, —Ç—Ä–∞–Ω—Å–ª—è—Ü–∏–∏ –±–∞—Å–∫–µ—Ç–±–æ–ª–∞, –æ–Ω–ª–∞–π–Ω —Ç—Ä–∞–Ω—Å–ª—è—Ü–∏–∏ —Ç–µ–Ω–Ω–∏—Å–∞, —Å–ø–æ—Ä—Ç–∏–≤–Ω—ã–µ –º–∞—Ç—á–∏ –æ–Ω–ª–∞–π–Ω, –ø—Ä–æ—Å–º–æ—Ç—Ä —Å–ø–æ—Ä—Ç–∏–≤–Ω—ã—Ö —Å–æ–±—ã—Ç–∏–π –≤ –∏–Ω—Ç–µ—Ä–Ω–µ—Ç–µ" />
            </Helmet>
            <h1 className="pageName">–û–Ω–ª–∞–π–Ω —Ç—Ä–∞–Ω—Å–ª—è—Ü–∏–∏ –º–∞—Ç—á–µ–π</h1>
            <div className="wrap">
                {item}
            </div>
        </div>
    );
};

export default Broadcasts;